<template>
	<div>
		<header>
			<router-link to='/' class="goback"></router-link>
			<p></p>
		</header>
		<div class="content">
			<div class="c_m">
				<p class="c_m_t"></p>
				<input type="text" name="" class="c_n" placeholder="" v-model='name'>
				<p class="c_m_t"></p>
				<div class="c_b">
					<input id='t_begin' type="text" name="" :data-lcalendar="cb_time" v-binit readonly v-model='b_time'>
					<p></p>
				</div>
				<div class="c_b">
					<input id='t_end' type="text" name="" :data-lcalendar="ce_time" v-einit readonly v-model='e_time'>
					<p></p>
				</div>
				<div class="c_py">
					<p  class="c_m_t">()</p>
					<textarea placeholder="..." v-model='tjy'></textarea>
					<button @click='_sY'></button>
				</div>
				<div class="yy" v-show='y_flag'>
					<button></button>
					<p>&nbsp;</p>
				</div>
				<router-link to='/fbrw/kh' class='c_nt'></router-link>
				<p class="c_t">3</p>
			</div>
		</div>
		<tips :msg='message' v-show='t_flag'></tips>
	</div>
</template>

<script>
	var timer=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				// 
				name: '',
				// 
				b_time: '',
				// 
				e_time: '',
				// 
				tjy: '',
				// 
				message: '',
				t_flag: false,
				// 
				cb_time: '',
				// 
				ce_time: '',
				// 
				y_flag: false
			}
		},
		created: function(){
			// 
			this.cb_time=this.util.getDate(true, 0)+",2100-12-31";
			this.ce_time=this.util.getDate(false, 1)+",2100-12-31";
			// 
			this.b_time=this.util.getDate(true, 0);
			this.e_time=this.util.getDate(false, 14);
		},
		methods: {
			_sY: function(){
				if(this.y_flag){
					this.y_flag=false;
				}else{
					this.y_flag=true;
				}
			}
		},
		watch: {
			b_time: function(){
				this.ce_time=this.b_time+",2100-12-31";
			}
		},
		directives: {
			binit: {
				inserted: function(el){
					var calendar = new lCalendar();
					calendar.init({
					    'trigger': '#t_begin',
					    'type': 'date'
					});
				}
			},
			einit: {
				inserted: function(el){
					var calendar = new lCalendar();
					calendar.init({
					    'trigger': '#t_end',
					    'type': 'date'
					});
				}
			}
		},
		// 
		beforeRouteEnter(to, from, next){
			// 
			if(from.path=='/fbrw/kh'){
				next(function(vm){
					var rwInfo=vm.util.getSessionData("rwinfo");
					if(rwInfo){
						vm.name=rwInfo.name;
						vm.b_time=rwInfo.b_time;
						vm.e_time=rwInfo.e_time;
						vm.tjy=rwInfo.tjy;
					}
				});
			}else{
				next();
			}
		},
		// 
		beforeRouteLeave(to, from, next){
			if(to.path=='/fbrw/kh'){
				if(this.name==''||this.b_time==''||this.e_time==''){
					var self=this;
					clearTimeout(timer);
					timer=null;
					this.message='';
					this.t_flag=true;
					timer=setTimeout(function(){
						self.t_flag=false;
					}, 2000);
					next(false);
				}else{
					// sessionStorage
					var rwInfo=this.util.getSessionData("rwinfo");
					if(rwInfo){
						rwInfo.name=this.name;
						rwInfo.b_time=this.b_time;
						rwInfo.e_time=this.e_time;
						rwInfo.tjy=this.tjy;
						this.util.setSessionData('rwinfo', rwInfo);
					}else{
						var rwInfo={};
						rwInfo.name=this.name;
						rwInfo.b_time=this.b_time;
						rwInfo.e_time=this.e_time;
						rwInfo.tjy=this.tjy;
						this.util.setSessionData('rwinfo', rwInfo);
					}
					next();
				}
			}else{
				this.util.removeSessionData('rwinfo');
				next();
			}
		},
		components: {
			'tips': tips
		}
	}
</script>

<style scoped>
	.content{
		position: absolute;
		top: 0.88rem;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		padding: 0.3rem;
	}
	.c_m{
		width: 100%;
		height: 100%;
		overflow: auto;
	}
	.c_n{
		width: 100%;
		border: 0px;
		border-bottom: 0.01rem solid #e7e7e7;
		height: 1rem;
		font-size: 0.33rem;
		margin-bottom: 0.8rem;
	}
	.c_b{
		width: 100%;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.c_b p{
		height: 1rem;
		line-height: 1rem;
		font-size: 0.35rem;
		color: #888888;
	}
	.c_b input{
		float: right;
		width: 4rem;
		height: 1rem;
		font-size: 0.33rem;
		border: 0px;
		text-align: right;
	}
	.c_py{
		position: relative;
		margin-top: 0.8rem;
		width: 100%;
	}
	.c_m_t{
		border-left: 0.1rem solid #40cc8b;
		font-size: 0.35rem;
		padding-left: 0.1rem;
	}
	.c_py textarea{
		margin-top: 0.3rem;
		width: 100%;
		resize: none;
		border-radius: 0.1rem;
		height: 3rem;
		border: 0.01rem solid #e7e7e7;
		padding: 0.2rem;
		font-size: 0.33rem;
	}
	.c_py button{
		border: 0px;
		background-color: rgba(0,0,0,0);
		position: absolute;
		bottom: 0.2rem;
		right: 0.15rem;
		width: 0.45rem;
		height: 0.65rem;
		background-image: url('../../static/images/ico_02.png');
		background-size: 100% 100%;
	}
	.c_nt{
		display: block;
		width: 3rem;
		height: 1rem;
		background-color: #40cc8b;
		color: #fff;
		font-size: 0.35rem;
		text-align: center;
		line-height: 1rem;
		border-radius: 3rem;
		margin: 0 auto;
		margin-top: 0.3rem;
	}
	.c_t{
		margin-top: 0.2rem;
		text-align: center;
		font-size: 0.3rem;
		color: #777777;
	}
	.yy{
		margin-top: 0.2rem;
		width: 100%;
	}
	.yy button{
		display: block;
		width: 1.8rem;
		height: 1.8rem;
		margin: 0 auto;
		background-image: url('../../static/images/yy.png');
		background-color: rgba(0,0,0,0);
		background-size: 100% 100%;
		border: 0px;
	}
	.yy p{
		font-size: 0.33rem;
		color: #555555;
		text-align: center;
		margin-top: 0.2rem;
	}
</style>


